import {
	mapState,
	mapActions,
	mapMutations
} from 'vuex'
import uniSearchBar from '../../components/uni-search-bar/uni-search-bar.vue'
export default {
	name: "",
	components: {
		uniSearchBar
	},
	props: {},
	data() {
		return {
			cValue: '',
			// 给导航栏添加样式的参数
			navNum: 0,
			// 请求小分类的id
			bigid: null
		}
	},
	methods: {
		...mapActions(['getBigClassfy', 'getSmallClassfy']),
		...mapMutations(['setBigId']),
		// 搜索框方法
		search() {

		},
		// 改变大分类时的方法
		changeBig(idx, id) {
			this.navNum = idx
			this.bigid = id
			this.setBigId(id)
			if (this.bigId) {
				this.getSmallClassfy(this.bigId)
			}
		},
		// 点击小分类跳转分类详情页
		goToClassfyD(detailId) {
			uni.navigateTo({
				url: `../classfydetail/classfydetail?id=${detailId}&bigid=${this.bigid}`,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	mounted() {
		this.getBigClassfy()
		setTimeout(() => {
			this.bigid = this.bigId
		}, 200)
	},
	onLoad() {

	},
	filters: {

	},
	computed: {
		...mapState(['bigClassfy', 'bigId', 'littleObj']),
	},
	watch: {

	},
	directives: {

	}
}
